.wall {
	display: flex;
	flex-direction: column;
	align-items: center;
	padding-bottom: 8rem;
}

.wall > .yb-card {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin: 2rem 6rem;
	width: auto;
	font-family: "Newsreader", sans-serif;
	transition: all ease 0.2s;
	text-decoration: none;
}

.yb-card > * {
	width: 50%;
}

.yb-card > .profile-picture {
	max-height: 36rem;
	object-fit: contain;
}

.description {
	margin-top: 2rem;
}

.description > p {
	margin: 0;
	color: #00000098;
}

.description > .name {
	margin-top: 1rem;
	font-size: larger;
	font-weight: bold;
}

.description > .nickname {
	color: #00000060;
}

.description > .quote {
	margin: 1rem 0;
	font-family: "Caveat", cursive;
}

.wall > .divider {
	width: 90%;
	margin: 2rem 0;
	border: 2px solid #bebebe70;
}

.wall > h1,
.wall > p {
	color: #00000098;
}

.wall > a {
	color: lightblue;
}

.wall > .holder {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	padding: 0 0 2rem 0;
}

.wall > .holder > .note-container {
	width: 24rem;
}

.wall button {
	text-decoration: none;
	font-family: "Newsreader", sans-serif;
	text-transform: uppercase;
	color: #707070;
	font-weight: bold;
	font-size: 12px;

	padding: 8px 24px;
	max-width: 16rem;

	background: none;

	border: 2px solid #707070;
	border-radius: 100px;
	outline: none;

	display: flex;
	align-items: center;
	justify-content: center;

	cursor: pointer;
	transition: all ease 0.2s;
}

.wall button:disabled {
	cursor: default;
	pointer-events: none;
	box-shadow: none;
	color: #70707040;
	border-color: #70707040;
}

.wall button svg {
	width: 18px;
	height: 18px;
	stroke-width: 3;
	margin-right: 0.5rem;
}

@media (hover: hover) {
	.wall button:hover {
		box-shadow: 3px 3px 0px rgba(0, 0, 0, 0.26);
		transform: translateY(-3px) translateX(-3px);
	}
	.wall button:hover {
		box-shadow: 3px 3px 0px rgba(0, 0, 0, 0.26);
		transform: translateY(-3px) translateX(-3px);
	}
	.wall button:active {
		box-shadow: none;
		transform: translateY(3px);
	}
}

@media (hover: none) {
	.wall button {
		-webkit-tap-highlight-color: transparent;
		box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.26);
	}
	.wall button:active {
		box-shadow: none;
		transform: translateY(3px);
	}
}

.wall .description p {
	font-size: 18px;
}

@media screen and (max-width: 768px) {
	.wall .yb-card {
		margin: 2rem;
		padding: 0;
		box-shadow: none;
		border-radius: 0;
	}

	.wall .description {
		width: 100%;
	}
}
