.preview-text {
	font-family: "Newsreader", "Roboto";
	color: #4b4b4b90;
	font-size: 14px;
	margin: 1rem 0;
}

.preview {
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 50%;
}

.note-container {
	/* background: wheat; */
	padding: 2rem;
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
}

.note-container > .note {
	background: rgb(241, 241, 128);
	max-width: 100%;
	width: 16rem;
	border-radius: 6px;
	box-shadow: 1px 1px 7px 0px grey;
	transition: all ease 0.5s;
}

.note-container:hover > .note {
	transform: rotate(0deg) scale(1.1);
	box-shadow: 2px 2px 7px 5px #70707050;
	cursor: default;
}

.note-container > .note > .note-header {
	height: 16px;
	background: green;
}

.note-container > .note > .text {
	padding: 1rem;
	font-size: 24px;
	color: #00000095;
	font-family: "Caveat", cursive;
	word-wrap: break-word;
	white-space: pre-wrap;
}

.note-rotation-0 {
	transform: rotate(2deg);
}
.note-rotation-1 {
	transform: rotate(2deg);
}
.note-rotation-2 {
	transform: rotate(4deg);
}
.note-rotation-3 {
	transform: rotate(6deg);
}
.note-rotation-4 {
	transform: rotate(8deg);
}
.note-rotation-5 {
	transform: rotate(10deg);
}
.note-rotation-6 {
	transform: rotate(12deg);
}
.note-rotation--1 {
	transform: rotate(-2deg);
}
.note-rotation--2 {
	transform: rotate(-4deg);
}
.note-rotation--3 {
	transform: rotate(-6deg);
}
.note-rotation--4 {
	transform: rotate(-8deg);
}
.note-rotation--5 {
	transform: rotate(-10deg);
}
.note-rotation--6 {
	transform: rotate(-12deg);
}


@media screen and (max-width: 768px) {
	#form {
		width: 100%;
	}

	.preview {
		width: 100%;
	}
}