@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Newsreader:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Caveat:wght@400;700&display=swap);
.App {
	text-align: center;
	min-height: 60vh;
	display: flex;
	flex-direction: column;
	align-items: center;
}

#timer {
	font-family: "Newsreader", "Roboto";
	color: #202020;
}

h1 {
	margin: 1rem;
	font-family: "Newsreader", "Roboto";
	font-weight: bold;
	color: #383838;
}

#timer > p {
	margin: 1rem;
	padding: 0 1rem;
	font-size: large;
}

#timer > span {
	margin: 0rem;
	margin-top: 1rem;
	color: #707070;
}

#timer > img {
	margin: 4rem;
	height: 200px;
}

.form-page {
	display: flex;
	width: 100%;
	padding: 0 2rem;
}

@media screen and (max-width: 768px) {
	.form-page {
		flex-direction: column;
	}
	#form {
		width: 100%;
	}
}

#form {
	padding: 1rem;
	width: 50%;
	font-family: "Newsreader", "Roboto";
	color: #202020;
	display: flex;
	flex-direction: column;
	align-items: center;
}

#form > p {
	font-family: "Newsreader", "Roboto";
	color: #4b4b4b90;
	font-size: 12px;
}

#form > * {
	margin: 1rem 0;
}

#form > .card {
	display: flex;
	flex-direction: column;
	border-radius: 0.5rem;
	padding: 1rem;
	width: 100%;
}

#form > .card > .divider {
	width: 100%;
	height: 2px;
	background-color: #bebebe90;
	margin: 1rem 0;
}

#form > .divider {
	width: 100%;
	margin: 0.5rem 0;
	border: 1px solid #bebebe70;
}

#form > .card > input,
#form > .card > select,
#form > .card > textarea,
#form > .card > button {
	font-family: "Newsreader", "Roboto";
	color: #202020;
}

#form > .card > input,
#form > .card > select,
#form > .card > textarea {
	border: 0;
	border-radius: 0.5rem;
	width: 100%;
}

#form > .card > input:focus,
#form > .card > select:focus,
#form > .card > textarea:focus {
	outline: none;
	box-shadow: none;
}

#form > .card > textarea {
	min-height: 20rem;
}

#form * button,
#form > label {
	text-decoration: none;
	text-transform: uppercase;
	font-family: "Newsreader", "Roboto";
	font-weight: bold;
	font-size: 14px;
	border: 2px solid #707070;
	color: #707070;
	background-color: white;
	border-radius: 100px;
	outline: none;
	cursor: pointer;

	padding: 6px 24px;
	margin: 0 0.5rem;
	transition: transform 0.3s, box-shadow 0.3s;
}

#form > label {
	margin-top: 1rem;
}

@media (hover: hover) {
	#form * button:hover,
	#form > label:hover {
		box-shadow: 3px 3px 0px rgba(0, 0, 0, 0.26);
		transform: translateY(-3px) translateX(-3px);
	}
}

#form > #error-para {
	color: red;
	font-weight: bold;
	margin: 0;
	font-size: 16px;
}

#form > .card > select {
	display: flex;
}

#yearbook > .holder {
	display: flex;
	flex-wrap: wrap;
	width: 100%;
	justify-content: center;
}

#yearbook > .holder > .yb-card {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin: 2rem;
	width: 20rem;
	max-width: 100%;
	font-family: "Newsreader", sans-serif;
	transition: all ease 0.2s;
	text-decoration: none;
	position: relative;
}

#yearbook > .holder > .yb-card:hover {
	transform: scale(1.1);
	cursor: pointer;
}


.yb-card .tooltiptext {
  visibility: hidden;
  text-align: center;
  border-radius: 6px;
  padding: 0.5rem ;
  position: absolute;
  z-index: 100;
  bottom: 105%;
	font-size: 10px;
	color: white;
	background-color: #00000050;
	font-weight: bold;
}

.yb-card .tooltiptext::after {
  content: "";
  position: absolute;
  right: 50%;
  top: 100%;
  border-width: 5px;
  border-style: solid;
  border-color: #00000050 transparent transparent transparent  ;
}

.yb-card:hover .tooltiptext {
  visibility: visible;
}

#yearbook > .holder > .yb-card > .profile-picture {
	height: 200px;
	width: 200px;
	object-fit: contain;
}

#yearbook > .holder > .yb-card > p
{
	margin: 0;
	color: #00000098;
}

#yearbook > .holder > .yb-card > .name {
	margin-top: 1rem;
	font-size: larger;
	font-weight: bold;
}

#yearbook > .holder > .yb-card > .nickname {
	color: #00000060;
}

#yearbook > .holder > .yb-card > .quote {
	margin: 1rem 0;
	font-family: "Caveat", cursive;
  font-size: 20px;
  width: 90%;
	white-space: pre-wrap;
}

.pagination {
	display: flex;
	align-items: center;
	justify-content: center;
	font-family: "Newsreader", sans-serif;
	margin-bottom: 8rem;
}

.pagination > * {
	margin: 0 16px;
}

.pagination > p {
	font-weight: bold;
	font-size: large;
	color: #707070;
}

.pagination > button {
	text-decoration: none;
	font-family: "Newsreader", sans-serif;
	text-transform: uppercase;
	color: #707070;
	font-weight: bold;
	font-size: 12px;

	padding: 7px 30px;
	max-width: 7rem;

	background: none;

	border: 2px solid #707070;
	border-radius: 100px;
	outline: none;

	display: flex;
	align-items: center;
	justify-content: center;

	cursor: pointer;
	transition: all ease 0.2s;
}

.pagination > button:disabled {
	cursor: default;
	pointer-events: none;
	box-shadow: none;
	color: #70707040;
	border-color: #70707040;
}

@media (hover: hover) {
	.pagination > button:hover {
		box-shadow: 3px 3px 0px rgba(0, 0, 0, 0.26);
		transform: translateY(-3px) translateX(-3px);
	}
	.pagination > button:hover {
		box-shadow: 3px 3px 0px rgba(0, 0, 0, 0.26);
		transform: translateY(-3px) translateX(-3px);
	}
	.pagination > button:active {
		box-shadow: none;
		transform: translateY(3px);
	}
}

@media (hover: none) {
	.pagination > button {
		-webkit-tap-highlight-color: transparent;
		box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.26);
	}
	.pagination > button:active {
		box-shadow: none;
		transform: translateY(3px);
	}
}

@media screen and (max-width: 768px) {
	.yb-card {
		padding: 2rem 0;
		border-radius: 5px;
		box-shadow: 2px 3px 6px 0px rgb(0 0 0 / 25%);
	}
}
.lds-ring {
	display: inline-block;
	position: relative;
	width: 80px;
	height: 80px;
}
.lds-ring div {
	box-sizing: border-box;
	display: block;
	position: absolute;
	width: 64px;
	height: 64px;
	margin: 8px;
	border: 8px solid #707070;
	border-radius: 50%;
	animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
	border-color: #707070 transparent transparent transparent;
}
.lds-ring div:nth-child(1) {
	animation-delay: -0.45s;
}
.lds-ring div:nth-child(2) {
	animation-delay: -0.3s;
}
.lds-ring div:nth-child(3) {
	animation-delay: -0.15s;
}
@keyframes lds-ring {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}

.preview-text {
	font-family: "Newsreader", "Roboto";
	color: #4b4b4b90;
	font-size: 14px;
	margin: 1rem 0;
}

.preview {
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 50%;
}

.note-container {
	/* background: wheat; */
	padding: 2rem;
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
}

.note-container > .note {
	background: rgb(241, 241, 128);
	max-width: 100%;
	width: 16rem;
	border-radius: 6px;
	box-shadow: 1px 1px 7px 0px grey;
	transition: all ease 0.5s;
}

.note-container:hover > .note {
	transform: rotate(0deg) scale(1.1);
	box-shadow: 2px 2px 7px 5px #70707050;
	cursor: default;
}

.note-container > .note > .note-header {
	height: 16px;
	background: green;
}

.note-container > .note > .text {
	padding: 1rem;
	font-size: 24px;
	color: #00000095;
	font-family: "Caveat", cursive;
	word-wrap: break-word;
	white-space: pre-wrap;
}

.note-rotation-0 {
	transform: rotate(2deg);
}
.note-rotation-1 {
	transform: rotate(2deg);
}
.note-rotation-2 {
	transform: rotate(4deg);
}
.note-rotation-3 {
	transform: rotate(6deg);
}
.note-rotation-4 {
	transform: rotate(8deg);
}
.note-rotation-5 {
	transform: rotate(10deg);
}
.note-rotation-6 {
	transform: rotate(12deg);
}
.note-rotation--1 {
	transform: rotate(-2deg);
}
.note-rotation--2 {
	transform: rotate(-4deg);
}
.note-rotation--3 {
	transform: rotate(-6deg);
}
.note-rotation--4 {
	transform: rotate(-8deg);
}
.note-rotation--5 {
	transform: rotate(-10deg);
}
.note-rotation--6 {
	transform: rotate(-12deg);
}


@media screen and (max-width: 768px) {
	#form {
		width: 100%;
	}

	.preview {
		width: 100%;
	}
}
.wall {
	display: flex;
	flex-direction: column;
	align-items: center;
	padding-bottom: 8rem;
}

.wall > .yb-card {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin: 2rem 6rem;
	width: auto;
	font-family: "Newsreader", sans-serif;
	transition: all ease 0.2s;
	text-decoration: none;
}

.yb-card > * {
	width: 50%;
}

.yb-card > .profile-picture {
	max-height: 36rem;
	object-fit: contain;
}

.description {
	margin-top: 2rem;
}

.description > p {
	margin: 0;
	color: #00000098;
}

.description > .name {
	margin-top: 1rem;
	font-size: larger;
	font-weight: bold;
}

.description > .nickname {
	color: #00000060;
}

.description > .quote {
	margin: 1rem 0;
	font-family: "Caveat", cursive;
}

.wall > .divider {
	width: 90%;
	margin: 2rem 0;
	border: 2px solid #bebebe70;
}

.wall > h1,
.wall > p {
	color: #00000098;
}

.wall > a {
	color: lightblue;
}

.wall > .holder {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	padding: 0 0 2rem 0;
}

.wall > .holder > .note-container {
	width: 24rem;
}

.wall button {
	text-decoration: none;
	font-family: "Newsreader", sans-serif;
	text-transform: uppercase;
	color: #707070;
	font-weight: bold;
	font-size: 12px;

	padding: 8px 24px;
	max-width: 16rem;

	background: none;

	border: 2px solid #707070;
	border-radius: 100px;
	outline: none;

	display: flex;
	align-items: center;
	justify-content: center;

	cursor: pointer;
	transition: all ease 0.2s;
}

.wall button:disabled {
	cursor: default;
	pointer-events: none;
	box-shadow: none;
	color: #70707040;
	border-color: #70707040;
}

.wall button svg {
	width: 18px;
	height: 18px;
	stroke-width: 3;
	margin-right: 0.5rem;
}

@media (hover: hover) {
	.wall button:hover {
		box-shadow: 3px 3px 0px rgba(0, 0, 0, 0.26);
		transform: translateY(-3px) translateX(-3px);
	}
	.wall button:hover {
		box-shadow: 3px 3px 0px rgba(0, 0, 0, 0.26);
		transform: translateY(-3px) translateX(-3px);
	}
	.wall button:active {
		box-shadow: none;
		transform: translateY(3px);
	}
}

@media (hover: none) {
	.wall button {
		-webkit-tap-highlight-color: transparent;
		box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.26);
	}
	.wall button:active {
		box-shadow: none;
		transform: translateY(3px);
	}
}

.wall .description p {
	font-size: 18px;
}

@media screen and (max-width: 768px) {
	.wall .yb-card {
		margin: 2rem;
		padding: 0;
		box-shadow: none;
		border-radius: 0;
	}

	.wall .description {
		width: 100%;
	}
}

