#yearbook > .holder {
	display: flex;
	flex-wrap: wrap;
	width: 100%;
	justify-content: center;
}

#yearbook > .holder > .yb-card {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin: 2rem;
	width: 20rem;
	max-width: 100%;
	font-family: "Newsreader", sans-serif;
	transition: all ease 0.2s;
	text-decoration: none;
	position: relative;
}

#yearbook > .holder > .yb-card:hover {
	transform: scale(1.1);
	cursor: pointer;
}


.yb-card .tooltiptext {
  visibility: hidden;
  text-align: center;
  border-radius: 6px;
  padding: 0.5rem ;
  position: absolute;
  z-index: 100;
  bottom: 105%;
	font-size: 10px;
	color: white;
	background-color: #00000050;
	font-weight: bold;
}

.yb-card .tooltiptext::after {
  content: "";
  position: absolute;
  right: 50%;
  top: 100%;
  border-width: 5px;
  border-style: solid;
  border-color: #00000050 transparent transparent transparent  ;
}

.yb-card:hover .tooltiptext {
  visibility: visible;
}

#yearbook > .holder > .yb-card > .profile-picture {
	height: 200px;
	width: 200px;
	object-fit: contain;
}

#yearbook > .holder > .yb-card > p
{
	margin: 0;
	color: #00000098;
}

#yearbook > .holder > .yb-card > .name {
	margin-top: 1rem;
	font-size: larger;
	font-weight: bold;
}

#yearbook > .holder > .yb-card > .nickname {
	color: #00000060;
}

#yearbook > .holder > .yb-card > .quote {
	margin: 1rem 0;
	font-family: "Caveat", cursive;
  font-size: 20px;
  width: 90%;
	white-space: pre-wrap;
}

.pagination {
	display: flex;
	align-items: center;
	justify-content: center;
	font-family: "Newsreader", sans-serif;
	margin-bottom: 8rem;
}

.pagination > * {
	margin: 0 16px;
}

.pagination > p {
	font-weight: bold;
	font-size: large;
	color: #707070;
}

.pagination > button {
	text-decoration: none;
	font-family: "Newsreader", sans-serif;
	text-transform: uppercase;
	color: #707070;
	font-weight: bold;
	font-size: 12px;

	padding: 7px 30px;
	max-width: 7rem;

	background: none;

	border: 2px solid #707070;
	border-radius: 100px;
	outline: none;

	display: flex;
	align-items: center;
	justify-content: center;

	cursor: pointer;
	transition: all ease 0.2s;
}

.pagination > button:disabled {
	cursor: default;
	pointer-events: none;
	box-shadow: none;
	color: #70707040;
	border-color: #70707040;
}

@media (hover: hover) {
	.pagination > button:hover {
		box-shadow: 3px 3px 0px rgba(0, 0, 0, 0.26);
		transform: translateY(-3px) translateX(-3px);
	}
	.pagination > button:hover {
		box-shadow: 3px 3px 0px rgba(0, 0, 0, 0.26);
		transform: translateY(-3px) translateX(-3px);
	}
	.pagination > button:active {
		box-shadow: none;
		transform: translateY(3px);
	}
}

@media (hover: none) {
	.pagination > button {
		-webkit-tap-highlight-color: transparent;
		box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.26);
	}
	.pagination > button:active {
		box-shadow: none;
		transform: translateY(3px);
	}
}

@media screen and (max-width: 768px) {
	.yb-card {
		padding: 2rem 0;
		border-radius: 5px;
		box-shadow: 2px 3px 6px 0px rgb(0 0 0 / 25%);
	}
}